import 'source-map-support/register';
import Express from 'express';
import _ from 'lodash';

import Config from '../config';

import * as allRoutes0 from "./routes/default.js"; import * as allRoutes1 from "./routes/statics.js"; let allRoutes = [allRoutes0, allRoutes1];

const app = new Express();

const init = async () => {

  //Server route definations
  _.each(
    _.sortBy( allRoutes, [
      ({ route, url }) => {
        return url;
      }
    ] ).reverse(),
    ( { route, url } ) => {
      if ( url ) {
        app.use( url, route );
      } else {
        app.use( route );
      }
    }
  );
  //----------------------
};

init().then( () => {
  app.listen( Config['webapp_port'], () => {
    console.log( "Application running", `PORT: ${Config['webapp_port']}` )
  } );
} );
